import React, { Component } from 'react';
import './CancelActivity.css';

import { Activity, User } from '../api'
import { Toast } from 'antd-mobile';

const activityTypes = {
  adult:"成年人活动",
  minor:"未成年人活动",
  "parent-child":"亲子活动"
}
const grades = ['幼儿园小班','幼儿园中班','幼儿园大班','一年级','二年级','三年级','四年级','五年级','六年级','七年级','八年级','九年级','高一','高二','高三'];

class CancelActivity extends Component {
  constructor(props){
      super(props);
      this.state = {
        activityContent: {},
        users:[],
        cancelBookedDisabled: false,

      };
  }

  timestampToTime = (timestamp) => {
        let date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        let Y = date.getFullYear() + '-';
        let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        let D = (date.getDate() < 10 ? '0' +(date.getDate()) : date.getDate())   + ' ';
        let h = date.getHours() + ':';
        let m = date.getMinutes();
        // let s = date.getSeconds();
        let week = this.getweekDay(new Date(Y+M+D));
        let time = (h<=12) ? "上午" : "下午";
        return Y+M+D+"("+week+") "+time+h+m;
  }

  getweekDay = (date) => {
    let week;
    if(date.getDay()===0) week="星期天"
    if(date.getDay()===1) week="星期一"
    if(date.getDay()===2) week="星期二"
    if(date.getDay()===3) week="星期三"
    if(date.getDay()===4) week="星期四"
    if(date.getDay()===5) week="星期五"
    if(date.getDay()===6) week="星期六"
    return week;
  }

  getGrade = (value) => {
    return grades[value];
  }

  async requestBookedData () {
    try{
      let data = await User.getMyBooked({
        _id : this.props.location.state.id,
      });
      if(data && data.code===true){
        this.setState({
          activityContent: data.data.activity,
          users: data.data.users
        });
      }
    }catch(e){
      //
    }
  }



  //耗时操作放在这里面
  async componentDidMount(){
    try{
      this.requestBookedData();

    }catch(e){
      //
    }
  }


  cancelBookedData = async () => {
    this.setState({
      cancelBookedDisabled: false
    });
    try{
      let data = await Activity.cancelBooked({
        _id: this.props.location.state.id,
      });

      Toast.success(data.msg, 2, () => {
        if (data.code === true) {

          this.props.history.goBack()

        }
      });

    }catch(e){
      //
    }
  }

  cancelBooked = () => {
    this.setState({
      cancelBookedDisabled: true
    })
    this.cancelBookedData();
  }

  mapper = (item, index) => { return (
    <div key={index} className="CancelActivity-canyuzhe-item ">{item.name}（{item.type === 0 ? "未成年人" : "成年人"}）</div>
  )}

  render() {
    let data = this.state.activityContent || {};

    return(
      <div className="CancelActivity-home">
        <div className="CancelActivity-top">
          <div>{data.title}</div>
          <div style={{display:`${this.props.location.state.model === "lecture" ? "block" : "none"}`}}>主讲嘉宾：{data.speaker}</div>
          <div>时间：{data.time ? this.timestampToTime(data.time.start): ''}</div>
          <div>地点：{data.address}</div>
          <div style={{display:`${this.props.location.state.model === "lecture" ? "none" : "block"}`}}>活动类型：{activityTypes[data.type]}</div>
          <div>{this.props.location.state.model === "lecture" ? "适合年龄段：" : "活动对象："}{data.limit? ((data.limit.type === 'age') ? (data.limit.age.min + "岁~" + data.limit.age.max + "岁") : (this.getGrade(data.limit.grade.min) + "~" + this.getGrade(data.limit.grade.max))) : ""}</div>
        </div>
        <div className="CancelActivity-lineStyle"></div>
        <div className="CancelActivity-canyuzhe">参与者<span className="CancelActivity-canyuzhe-number">({this.state.users.length}人)</span></div>
        <div className="CancelActivity-canyuzhe-list">{this.state.users.map(this.mapper)}</div>
        <button className="CancelActivity-Button" onClick={this.cancelBooked} disabled={this.state.cancelBookedDisabled}>取消预约</button>

      </div>
    );
  }
}

export default CancelActivity;



