import React, { Component } from 'react';
import './ActivityContent.css';
import { Link } from 'react-router-dom';
import { Activity } from '../api'

const pathPrefix = window._config.pathPrefix;

const img_1 = '/static/activity/baoming_yue_1.png';
const img_2 = '/static/activity/baoming_yue_2.png';
const btn_play = '/static/activity/btn_play1.png';
const btn_stop = '/static/activity/btn_stop1.png';

const toast = [<p key="1">    1、请报名者确定能参加再报名，避免造成资源浪费。报名成功但临时不能参加者请在活动开始3日前通过微信自行取消预约（如活动是1月4日，那么须在1月1日24:00前取消），活动开始时未到场且未在规定时间内取消预约者，三个月内将无法再报名参加博物馆的教育活动。谢谢配合。</p>,
               <p key="2">    2、参与者的报名信息必须详细完整、真实可信。</p>,
               <p key="3">    3、参与者请于活动当天提前15分钟到常州博物馆一楼启明星活动中心门口签到，收费活动请缴费，并签署纸质版《常州博物馆社教活动安全责任协议书》（电子版见下一步弹窗，签到时正式签署。参与者若未满18周岁，应在监护人或临时监护人的陪同下到场，由监护人或临时监护人代签）。</p>,
               <p key="4">    4、参加者在活动中请自觉遵守纪律，不大声喧哗，不追逐嬉戏。</p>,
               <p key="5">    5、活动遇暴雨及其他恶劣天气顺延，时间另行通知。</p>];
const toast2 = [<p key="1"> 为明确甲乙双方的安全责任，保护甲乙双方的合法权益，保证活动的顺利开展，依据有关法律法规，甲乙双方达成如下安全责任协议：</p>,
                <p key="2"> 一、甲方责任 </p>,
                <p key="3"> 1.组织开展该项活动。</p>,
                <p key="4"> 2.保证活动工具、材料等符合安全要求。</p>,
                <p key="5"> 3.明确告知乙方各项安全注意事项。</p>,
                <p key="6"> 二、乙方责任  </p>,
                <p key="7"> 1.在甲方指定地点和区域活动，有序并认真听取各项操作指导，按规定进行。无论何时、何地、何事，严禁单独行动，如果离开指定范围，自愿承担责任，并请示负责老师同意，归队时立即销假。</p>,
                <p key="8"> 2.活动中不得有任何危及他人或自身安全的行为。不在危险的地方攀爬和拍照留影，不私自玩水、玩火等。如发生意外事故，立即告知工作人员做应急处理。</p>,
                <p key="9"> 3.食用自带食品及自购食品，请注意饮食卫生，安全责任自负。</p>,
                <p key="10"> 4.不携带贵重物品，妥善保管财物。</p>,
                <p key="11"> 5.不得隐瞒自己的病史，如身体有任何不适或其它特殊情况，须及时向随队工作人员报告。 </p>,
                <p key="12"> 三、其它责任  因乙方自发、或不可抗力、或第三方原因导致的乙方生病、受伤以及经济损失等情况，甲方不承担责任。如乙方生病、受伤以及经济损失等情况由第三方导致，则由乙方和第三方协商解决。</p>,
                <p key="13"> 四、本协议有效期从乙方签到时生效，签退时自动失效。提前到达或延后离开期间，责任由乙方自负。</p>,
                <p key="14"> 五、本协议随报名通知一起发布，乙方报名即视为自动认可，签到时正式签署。乙方如未满18周岁，应在监护人或临时监护人的陪同下到场，由监护人或临时监护人代签。本协议一式两份，双方签名盖章后有效，各执一份。</p>];

const grades = ['幼儿园小班','幼儿园中班','幼儿园大班','一年级','二年级','三年级','四年级','五年级','六年级','七年级','八年级','九年级','高一','高二','高三'];

class ActivityContent extends Component {

    constructor(props){
        super(props);

        this.state = {
          result:{},
          click_Baoming: false,
          is_read: false,
          is_next: false,
          makeReservation: true,
          videoIsPause: true,
          audioIsPause: true,
          };
    }
    timestampToTime = (timestamp) => {
          let date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
          let Y = date.getFullYear() + '-';
          let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
          let D = (date.getDate() < 10 ? '0' +(date.getDate()) : date.getDate())   + ' ';
          let h = date.getHours() + ':';
          let m = date.getMinutes();
          // let s = date.getSeconds();
          let week = this.getweekDay(new Date(Y+M+D));
          let time = (h<=12) ? "上午" : "下午";
          return Y+M+D+"("+week+") "+time+h+m;
    }

    getweekDay = (date) => {
      let week;
      if(date.getDay()===0) week="星期天"
      if(date.getDay()===1) week="星期一"
      if(date.getDay()===2) week="星期二"
      if(date.getDay()===3) week="星期三"
      if(date.getDay()===4) week="星期四"
      if(date.getDay()===5) week="星期五"
      if(date.getDay()===6) week="星期六"
      return week;
    }

    getGrade = (value) => {
      return grades[value];
    }

    //耗时操作放在这里面
    async componentDidMount(){
      try{
        let data = await Activity.activityContent({
          _id : this.props.location.state.id,
        });
        if (data.data.reservation && data.data.reservation.limit > 0) {
          if (data.data.reservation.count&&data.data.reservation.count>data.data.reservation.limit) {
            this.setState({
              makeReservation: false,
            })
          }
        }
        this.setState({
          result: data.data,
        })
      }catch(e){
        //
      }
    }

    baoming = (value) => {
      if (value === "活动预约报名") {
        this.setState({
          click_Baoming: true,
        })
      }
    }


    cancel_baoming = () => {
      this.setState({
        click_Baoming: false,
        is_read: false,
        is_next: false,
      })
    }

    read = () => {
      this.setState({
        is_read: !this.state.is_read,
      })
    }

    next = () => {
      if (this.state.is_read) {
        this.setState({
          is_next: true,
        })
      }
    }
  render() {
    let data = this.state.result || {};

    let pathname = `${pathPrefix}/signUp/${data._id}`;
    let LinkData = {
      id: data._id,
      model:"education"
    }
    var path = {
     pathname:pathname,
     state:LinkData,
   }

    return(
      <div className="activity-content-home">
        <div className="activity-toast" style={{display:`${this.state.click_Baoming ? "block" : "none"}`}}>
          <div className="activity-toast-div">
            <div className="activity-toast-div-title">{this.state.is_next?"常州博物馆社教活动安全责任协议书":"温馨提示"}</div>
            <div className="activity-toast-div-line"></div>
            <div className="activity-toast-div-content">{this.state.is_next?toast2:toast}</div>
            {(this.state.is_next) ? (
              <div className="activity-toast-div-footer">
                <div className="activity-toast-div-footer-next-noagree" onClick={this.cancel_baoming}>不同意</div>
                <Link to={path}>
                  <div className="activity-toast-div-footer-next-agree">同意</div>
                </Link>
              </div>
            ) : (
              <div className="activity-toast-div-footer">
                <div className="activity-toast-div-footer-yiyue" onClick={this.read} style={{background: `url(${this.state.is_read?img_2:img_1})   no-repeat  center / 100% 100%`, display:`${this.state.is_next ? "none" : "block"}`}}></div>
                <div className={`${this.state.is_read?"activity-toast-div-footer-next-isread":"activity-toast-div-footer-next-noread"} + " activity-toast-div-footer-next-isread"`} onClick={this.next} style={{display:`${this.state.is_next ? "none" : "block"}`}}>下一步</div>
              </div>
              )}
          </div>
          <div className="activity-toast-cancelbutton" onClick={this.cancel_baoming}></div>
        </div>
        <div className="activity-content-title">{data.title}</div>
        <div className="line"></div>
        <div className="activity-content-info">
          <div>活动时间：{data.time ? this.timestampToTime(data.time.start): ''}</div>
          <div>活动地点：{data.address}</div>
          <div>活动对象：{data.limit? ((data.limit.type === 'age') ? (data.limit.age.min + "岁~" + data.limit.age.max + "岁，" + data.reservation.limit + "名") : (this.getGrade(data.limit.grade.min) + "~" + this.getGrade(data.limit.grade.max) + "，" + data.reservation.limit + "名")) : ""}</div>
          <div>活动费用：{data.cost}</div>
          <div style={{color:`${this.state.makeReservation ?  '#75ABED' : '#999999'}`}}>{this.state.makeReservation ? '预约中' : '预约结束'}：报名人数（{data.reservation&&data.reservation.limit>0 ? ((data.reservation.count ? data.reservation.count : '0') + '/' +data.reservation.limit) : ""}）</div>
        </div>
        <div className="activity-content-cover" style={{background: `url(${data.cover}) center no-repeat / cover`}}></div>
        <div className="activity-content-digest">{data.intro}</div>
        {data.videos && data.videos.length > 0? (<div className="activity-content-video-div">

          <video ref={ref=>this.video=ref} className="activity-content-video" src={`${data.videos?data.videos[0].src : ""}`} onClick={()=>{
            this.video.pause();
            this.setState({
              videoIsPause:true
            })
          }}
          onEnded={() => {
            this.setState({
              videoIsPause:true
            })
          }}></video>
          <div className="activity-content-video-mengban" style={{display:`${this.state.videoIsPause ? "block" : "none"}`}}>
            <div className="activity-content-video-mengban-button" onClick={()=>{
              this.video.play();
              this.setState({
                videoIsPause:false
              })
            }}></div>
          </div>
          <div className="activity-content-videotitle" >{data.videos?data.videos[0].title : ""}</div>
        </div>) : null}

        {data.audios && data.audios.length > 0? (<div className="activity-content-audio-div">
          <audio ref={ref=>this.audio=ref} style={{display:"none"}} src={`${data.audios?data.audios[0].src : ""}`} onEnded={() => {
            this.setState({
              audioIsPause:true
            })
          }}></audio>
          <div className="activity-content-audio-title">{data.audios?data.audios[0].title : ""}</div>
          <div className="activity-content-audio-button" style={{background: `url(${this.state.audioIsPause?btn_play:btn_stop})   no-repeat  center / 100% 100%` }} onClick={()=>{
            if (this.state.audioIsPause) {
              this.audio.play();
              this.setState({
                audioIsPause:false
              })
            }else {
              this.audio.pause();
              this.setState({
                audioIsPause:true
              })
            }

          }}></div>
        </div>) : null}

        <div className="activity-content-content">
          <div className="activity-content-content-title">活动内容</div>
          <div className="activity-content-content-line"></div>
          <div className="activity-content-content-digest" dangerouslySetInnerHTML={{__html: data.content}}/>
        </div>
        <div className="activity-footer">
          <div className="bg-cc footer-image"></div>
        </div>
        <div className={`${data.time ? (data.time.start - (new Date().getTime()) < 0) ? "activity-footer-jieshu": (data.reservation.count ?  (data.reservation.count >= data.reservation.limit ? "activity-footer-man":"activity-footer-baoming") : "activity-footer-baoming"): ''}`} onClick={(e) => this.baoming(e.target.innerHTML)}>
        {data.time ? (data.time.start - (new Date().getTime()) < 0) ? "已结束": (data.reservation.count ?  (data.reservation.count >= data.reservation.limit ? "活动预约已满":"活动预约报名") : "活动预约报名"): ''}
        </div>
      </div>
    );
  }
}

export default ActivityContent;



