import React, { Component } from 'react';
import './PeopleInfo.css';
import { InputItem, Picker, List, Toast} from 'antd-mobile';
import { createForm } from 'rc-form';
import { User } from '../api'
import { nationList ,gradeList} from './global'


class People extends Component {

  postUserInfo = () => {

      let postData = {
        name:this.state.nameData,
        // age:parseInt(this.state.ageData, 10),
        sex: this.state.sexData,
        nation: this.state.nationData,
        idCard: this.state.IDCardData,
        tel:this.state.phoneData,
        type:this.state.peopleType
      }

      if (postData.type === 0) {
        postData.school = this.state.schoolData;
        postData.grade = this.state.gradeData;
        postData.guardian = {
          name:this.state.jianhurenNameData,
          idCard:this.state.jianhurenIDCardData
        };
      }else {
        postData.work = this.state.workData;
      }


      //console.log(postData);
      if (this.props.location.state) {
        this.postEditUserInfoData(postData);

      }else {
        this.postUserInfoData(postData);

      }
  }

  async postEditUserInfoData (value) {
    try{
      value._id = this.props.location.state.Info._id;
      let data = await User.editUser(value);
      //console.log(data);
      if (data) {
        Toast.success(data.msg, 2, () => {
          if (data.code === true) {
            this.props.history.goBack()
          }
        });
      }

    }catch(e){
      //
    }
  }

  async postUserInfoData (value) {
    try{
      let data = await User.addUser(value);
      //console.log(data);
      if (data) {
        Toast.success(data.msg, 2, () => {
          if (data.code === true) {
            this.props.history.goBack()
          }
        });
      }

    }catch(e){
      //
    }
  }

  constructor(props){
      super(props);
      //console.log(props);
      this.state = {
        nameData: this.props.location.state ? this.props.location.state.Info.name : "",
        ageData: this.props.location.state ? this.props.location.state.Info.age : null,
        sexData: this.props.location.state ? this.props.location.state.Info.sex : 1,
        nationData: this.props.location.state ? this.props.location.state.Info.nation : "",
        IDCardData: this.props.location.state ? this.props.location.state.Info.idCard : "",
        phoneData:this.props.location.state ? this.props.location.state.Info.tel : null,
        peopleType: this.props.location.state ? this.props.location.state.Info.type : null,
        schoolData: this.props.location.state ? this.props.location.state.Info.school : "",
        gradeData: this.props.location.state ? this.props.location.state.Info.grade : "",
        jianhurenNameData: this.props.location.state ? (this.props.location.state.Info.guardian ? this.props.location.state.Info.guardian.name : "") : "",
        jianhurenIDCardData: this.props.location.state ? (this.props.location.state.Info.guardian ? this.props.location.state.Info.guardian.idCard : "") : "",
        workData: this.props.location.state ? this.props.location.state.Info.work : "",

        hasIDCardError: false,
        hasPhoneError: false,
        hasjhrIDCardError: false,
      };
  }

  onIDCardErrorClick = () => {
    if (this.state.hasIDCardError) {
      Toast.info('请输入正确身份证号');
    }
  }

  onPhoneErrorClick = () => {
    if (this.state.hasPhoneError) {
      Toast.info('请输入正确手机号码');
    }
  }

  onjhrIDCardErrorClick = () => {
    if (this.state.hasjhrIDCardError) {
      Toast.info('请输入正确身份证号');
    }
  }

  onIDCardBlur = (value) => {
    const reg = /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}[0-9Xx]$)/;
    if (reg.test(value) === false) {
      this.setState({
        hasIDCardError: true,
      });
    } else {
      this.setState({
        hasIDCardError: false,
      });
    }
  }

  onPhoneBlur = (value) => {
    if (value.replace(/\s/g, '').length < 11) {
       this.setState({
         hasPhoneError: true,
       });
    } else {
       this.setState({
         hasPhoneError: false,
       });
    }
  }


  onjhrIDCardBlur = (value) => {
    const reg = /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}[0-9Xx]$)/;
    if (reg.test(value) === false) {
      this.setState({
        hasjhrIDCardError: true,
      });
    } else {
      this.setState({
        hasjhrIDCardError: false,
      });
    }
  }

  onChangeSex = (value) => {
      this.setState({
        sexData : value
      })
  }

  onChangePeopleType = (value) => {
      this.setState({
        peopleType : value
      })
  }

  render() {
    const { getFieldProps } = this.props.form;
    return(
      <div className="PeopleInfo-home">

        <div className="list-name-div">
          <InputItem
                     onChange={(v) => { this.setState({
                                          nameData: v
                                        })
                                      }}
                     value={this.state.nameData}
                   >姓名</InputItem>
          <div className="PeopleInfo-home-line"></div>
        </div>

        {
          // <div className="list-age-div">
          // <InputItem
          //            onChange={(v) => { this.setState({
          //                                 ageData: v
          //                               })
          //                             }}
          //            type="number"
          //            maxLength={3}
          //            value={this.state.ageData}
          //          >年龄（周岁）</InputItem>
          //   <div className="PeopleInfo-home-line"></div>
          // </div>
        }

        <div className="list-sex-div">
          <div className="list-sex-div-content">
            <div className="sex-title">性别</div>
            <div className={this.state.sexData === 0 ? "sex-select" : "sex-normal"} onClick={() => this.onChangeSex(0)}>女</div>
            <div className={this.state.sexData === 1 ? "sex-select" : "sex-normal"} onClick={() => this.onChangeSex(1)}>男</div>

          </div>
          <div className="PeopleInfo-home-line"></div>
        </div>

        <div className="list-nation-div">
          <Picker data={nationList} cols={1} {...getFieldProps('district3')} className="forss"
            value={[this.state.nationData]}
            onOk={(v) => { this.setState({
                 nationData: v[0]
               })
             }}>

            <List.Item arrow="horizontal"

            >民族</List.Item>
          </Picker>
          <div className="PeopleInfo-home-line"></div>
        </div>

        <div className="list-idcard-div">
          <InputItem
                     value={this.state.IDCardData}
                     maxLength={18}
                     onBlur={this.onIDCardBlur}
                     error={this.state.hasIDCardError}
                     onErrorClick={this.onIDCardErrorClick}
                     onChange={(v) => {
                                          this.setState({
                                            IDCardData: v
                                          })
                                      }}
                   >身份证号</InputItem>
          <div className="PeopleInfo-home-line"></div>
        </div>

        <div className="list-phone-div">
          <InputItem
                     value={this.state.phoneData}
                     type="phone"
                     onBlur={this.onPhoneBlur}
                     error={this.state.hasPhoneError}
                     onErrorClick={this.onPhoneErrorClick}
                     onChange={(v) => {
                                          this.setState({
                                            phoneData: v
                                          })
                                      }}
                   >手机号码</InputItem>
        </div>

        <div className="list-cut-line"></div>

        <div className="PeopleInfo-PeopleType">
          <div className="PeopleInfo-PeopleType-young" onClick={() => this.onChangePeopleType(0)}>
          <div className={this.state.peopleType === 0 ? "PeopleInfo-PeopleType-img-click" : "PeopleInfo-PeopleType-img-normal"}></div>
            <div className="PeopleInfo-PeopleType-digest">未成年人</div>
          </div>
          <div className="PeopleInfo-PeopleType-adult" onClick={() => this.onChangePeopleType(1)}>
            <div className={this.state.peopleType === 1 ? "PeopleInfo-PeopleType-img-click" : "PeopleInfo-PeopleType-img-normal"} ></div>
            <div className="PeopleInfo-PeopleType-digest">成年人</div>
          </div>
        </div>

        <div className="PeopleInfo-PeopleType-young-footer" style={{display:`${this.state.peopleType === 0 ? "block" : "none"}`}}>

          <div className="list-school-div">
            <InputItem
                        value={this.state.schoolData}
                        onChange={(v) => {
                                             this.setState({
                                               schoolData: v
                                             })
                                         }}
                     >学校</InputItem>
            <div className="PeopleInfo-home-line"></div>
          </div>

          <div className="list-grade-div">
            <Picker data={gradeList} cols={1} {...getFieldProps('district3')} className="forss"
            value={[this.state.gradeData]}
            onOk={(v) => { this.setState({
                 gradeData: v[0]
               })
             }}>
              <List.Item arrow="horizontal">年级</List.Item>
            </Picker>
            <div className="PeopleInfo-home-line"></div>
          </div>

          <div className="list-jianhuren-name-div">
            <InputItem
                        value={this.state.jianhurenNameData}
                        onChange={(v) => {
                                             this.setState({
                                               jianhurenNameData: v
                                             })
                                         }}
                     >监护人姓名</InputItem>
            <div className="PeopleInfo-home-line"></div>
          </div>

          <div className="list-jianhuren-phone-div">
            <InputItem
                      value={this.state.jianhurenIDCardData}
                      maxLength={18}
                      onBlur={this.onjhrIDCardBlur}
                      error={this.state.hasjhrIDCardError}
                      onErrorClick={this.onjhrIDCardErrorClick}
                      onChange={(v) => {
                                           this.setState({
                                             jianhurenIDCardData: v
                                           })
                                       }}
                     >监护人身份证号</InputItem>
          </div>

        </div>

        <div className="PeopleInfo-PeopleType-adult-footer" style={{display:`${this.state.peopleType === 1 ? "block" : "none"}`}}>
          <div className="list-school-div">
            <InputItem
                        value={this.state.workData}
                        onChange={(v) => {
                                             this.setState({
                                               workData: v
                                             })
                                         }}
                            >工作单位／学校</InputItem>
            <div className="PeopleInfo-home-line"></div>
          </div>
        </div>
        <div className="PeopleInfo-add-button" onClick={this.postUserInfo}>{this.props.location.state ? "确认修改" : "确认添加"}</div>
      </div>
    );
  }
}
const PeopleInfo = createForm()(People);
export default PeopleInfo;



