import React, { Component } from 'react';
import './LectureModel.css';
import { ListView } from 'antd-mobile';
import { Link } from 'react-router-dom';
import { Activity } from '../api'
const pathPrefix = window._config.pathPrefix;

const limit = 5;

class LectureModel extends Component {

  constructor(props) {
    super(props);
    const dataSource = new ListView.DataSource({
      rowHasChanged: (row1, row2) => row1 !== row2,
  });

  this.state = {
      items: [],
      dataSource,
      isLoading: true,
      hasMore: true,
      page: 1,
      height: '',
    };
  }

  timestampToTime = (timestamp) => {
        let date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        let Y = date.getFullYear() + '-';
        let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        let D = (date.getDate() < 10 ? '0' +(date.getDate()) : date.getDate())   + ' ';
        let h = date.getHours() + ':';
        let m = date.getMinutes();
        // let s = date.getSeconds();
        let week = this.getweekDay(new Date(Y+M+D));
        let time = (h<=12) ? "上午" : "下午";
        return Y+M+D+"("+week+") "+time+h+m;
  }

  Biaoqian_timestampToTime = (timestamp) => {
        let date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        let Y = date.getFullYear() + '.';
        let M = date.getMonth()+1;
        return Y+M;
  }

  getweekDay = (date) => {
    let week;
    if(date.getDay()===0) week="星期天"
    if(date.getDay()===1) week="星期一"
    if(date.getDay()===2) week="星期二"
    if(date.getDay()===3) week="星期三"
    if(date.getDay()===4) week="星期四"
    if(date.getDay()===5) week="星期五"
    if(date.getDay()===6) week="星期六"
    return week;
  }

  onEndReached = (event) => {
   // load new data
   // hasMore: from backend data, indicates whether it is the last page, here is false
   if (this.state.isLoading && !this.state.hasMore) {
     return;
   }
   if (this.state.hasMore) {
     this.setState({ isLoading: true });
     this.requestApplyData();
   }
 }

 async requestApplyData () {
   try{
     let data = await Activity.list({
       limit : limit,
       page : this.state.page,
       category : "lecture",
       time : "future"
     });
     if(data && data.code===true){
      let rData = [...this.state.items, ...data.data];
      this.setState({
        items: rData,
        dataSource: this.state.dataSource.cloneWithRows(rData),
        isLoading: false,
        hasMore: (data.data.length >= limit)? true : false,
        page: this.state.page + 1,
      });
     }
   }catch(e){
     //

   }
 }


 //耗时操作放在这里面
 async componentDidMount(){
   try{
     let data = await Activity.list({
       limit : limit,
       page : this.state.page,
       category : "lecture",
       time : "future"
     });
     if(data && data.code===true){

       this.setState({
         dataSource: this.state.dataSource.cloneWithRows(data.data),
         items: data.data,
         isLoading: false,
         hasMore: (data.data.length >= limit)? true : false,
         page: this.state.page + 1
       })
     }
   }catch(e){
     //


   }
 }


  render() {

    let index = this.state.items.length - 1;
    const row = (rowData, sectionID, rowID) => {
      if (index < 0) {
      index = this.state.items.length - 1;
      }
      const obj = this.state.items[index--];
     let pathname = `${pathPrefix}/LectureContent/${obj._id}`;
     let data = {
       id: obj._id,
       model: 'lecture'
     }
     var path = {
      pathname:pathname,
      state:data,
    }

     return (
       <Link to={path}>
         <div className="lecture-card" key={rowID}>
            <div className="lecture-item">
                <div className="lecture-item-biaoji">
                  <div className="lecture-item-biaoji-time">{this.Biaoqian_timestampToTime(obj.time.start)}</div>
                </div>
                <div className="lecture-item-title">{obj.title}<span className="lecture-item-age">    (适合年龄：{obj.limit.age.min}~{obj.limit.age.max})</span></div>
                <div className="lecture-item-hdline"></div>
                <div className="lecture-item-content">
                    <div className="lecture-item-content-cover" style={{background: `url(${obj.cover}) center no-repeat / cover`}}></div>
                    <div className="lecture-item-content-info">
                        <div className="lecture-item-content-info-people">主讲嘉宾：{obj.speaker}</div>
                        <div className="lecture-item-content-info-time"><div style={{height:"24px", width:"70px"}}>讲座时间：</div><div style={{width:"calc(100% - 64px)"}}>{this.timestampToTime(obj.time.start)}</div></div>
                        <div className="lecture-item-content-info-place"><div style={{height:"24px", width:"70px"}}>讲座地点：</div><div style={{width:"calc(100% - 64px)"}}>{obj.address}</div></div>
                        <div className="lecture-item-content-info-state" style={{color:`${obj.reservation.count ?  (obj.reservation.count >= obj.reservation.limit ? '#999999':'#75ABED') : '#75ABED'}`}}>预约中：报名人数（{obj.reservation.count ? obj.reservation.count : 0}/{obj.reservation.limit}）</div>
                    </div>
                </div>
            </div>
         </div>
        </Link>
     );
   };

    return(
        <div className="lecture-cell">
          <ListView
                 ref={el => this.lv = el}
                 dataSource={this.state.dataSource}
                 renderHeader={() => (<div></div>)}
                 renderFooter={() => (
                 <div>
                   <div style={{ padding: 0, textAlign: 'center' }}>
                     {this.state.isLoading ? '加载中...' : (this.state.hasMore ? '加载更多' : '没有更多数据')}
                   </div>
                   <div className="lecture-footer">
                     <div className="bg-cc footer-image"></div>
                   </div>
                 </div>
                 )}
                 renderRow={row}
                 className="LectureModel-list"
                 pageSize={5}
                 // useBodyScroll
                 style={{
                   height: 'calc(100vh - 45px) ',
                   overflow: 'auto',
                 }}
                 onScroll={() => { }}
                 scrollRenderAheadDistance={500}
                 onEndReached={this.onEndReached}
                 onEndReachedThreshold={10}
               />
        </div>


    );
  }
}

export default LectureModel;



