import React, { Component } from 'react';
import './UserFinishActivity.css';
import { ListView } from 'antd-mobile';
import { Link } from 'react-router-dom';
import { User } from '../api'


const pathPrefix = window._config.pathPrefix;
const limit = 5;


class UserFinishActivity extends Component {

  constructor(props) {
    super(props);
    const dataSource = new ListView.DataSource({
      rowHasChanged: (row1, row2) => row1 !== row2,
  });

  this.state = {
      items: [],
      dataSource,
      isLoading: true,
      hasMore: true,
      page: 1,
      height: '',
    };
  }


    timestampToTime = (timestamp) => {
          let date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
          let Y = date.getFullYear() + '-';
          let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
          let D = (date.getDate() < 10 ? '0' +(date.getDate()) : date.getDate())   + ' ';
          let h = date.getHours() + ':';
          let m = date.getMinutes();
          // let s = date.getSeconds();
          let week = this.getweekDay(new Date(Y+M+D));
          let time = (h<=12) ? "上午" : "下午";
          return Y+M+D+"("+week+") "+time+h+m;
    }

    getweekDay = (date) => {
      let week;
      if(date.getDay()===0) week="星期天"
      if(date.getDay()===1) week="星期一"
      if(date.getDay()===2) week="星期二"
      if(date.getDay()===3) week="星期三"
      if(date.getDay()===4) week="星期四"
      if(date.getDay()===5) week="星期五"
      if(date.getDay()===6) week="星期六"
      return week;
    }


    onEndReached = (event) => {
     // load new data
     // hasMore: from backend data, indicates whether it is the last page, here is false
     if (this.state.isLoading && !this.state.hasMore) {
       return;
     }
     if (this.state.hasMore) {
       this.setState({ isLoading: true });
       this.requestApplyData();
     }
   }

   async requestApplyData () {
     try{
       let data = await User.getMyBookeds({
         visited: true,
         limit : limit,
         page : this.state.page,
       });
       if(data){
         if (data.code===true) {
           let rData = [...this.state.items, ...data.data];
           this.setState({
             items: rData,
             dataSource: this.state.dataSource.cloneWithRows(rData),
             isLoading: false,
             hasMore: (data.data.length >= limit)? true : false,
             page: this.state.page + 1,
           });
         }else if(data.code===false){
           this.setState({
             isLoading: false,
             hasMore: false,
           });
         }
       }
     }catch(e){
       //

     }
   }



    //耗时操作放在这里面
    async componentDidMount(){
      try{
        let data = await User.getMyBookeds({
          visited: true,
          limit : limit,
          page : this.state.page,
        });
        if (data) {
          if (data.code===true) {
            this.setState({
              dataSource: this.state.dataSource.cloneWithRows(data.data),
              items: data.data,
              isLoading: false,
              hasMore: (data.data.length >= limit)? true : false,
              page: this.state.page + 1
            })
          }else if(data.code===false) {
            this.setState({
              isLoading: false,
              hasMore: false,
            });
          }
        }
      }catch(e){
        //
      }
    }

  render() {

    let index = this.state.items.length - 1;
    const row = (rowData, sectionID, rowID) => {
      if (index < 0) {
      index = this.state.items.length - 1;
      }
      const obj = this.state.items[index--];
       let data = {
         id: obj.activity._id,
         model: obj.activity.category.name === "education" ? 'activity' : 'lecture'
       }
       var path = {
        pathname:obj.activity.category.name === "education" ? `${pathPrefix}/ActivityContent/${obj.activity._id}`: `${pathPrefix}/LectureContent/${obj.activity._id}`,
        state:data,
      }

     return (
       <Link to={path}>
         <div className="userFinishActivity-list-cell" key={rowID}>
           <div className="userFinishActivity-list-cell-title">{obj.activity.title}</div>
           <div className="userFinishActivity-list-cell-info">
              <div>活动时间：{this.timestampToTime(obj.activity.time.start)}</div>
              <div>活动地点：{obj.activity.address}</div>
           </div>
           <div className="userFinishActivity-list-cell-state">参与状态：{obj.visited ? "成功参与" : "未参与"}</div>
         </div>
       </Link>
     );
   };

    return(
      <div className="userFinishActivity-model">
        <div className="userFinishActivity-cell">
          <ListView
                 ref={el => this.lv = el}
                 dataSource={this.state.dataSource}
                 renderFooter={() => (
                 <div>
                   <div style={{ padding: 0, textAlign: 'center' }}>
                     {this.state.isLoading ? '加载中...' : (this.state.hasMore ? '加载更多' : '没有更多数据')}
                   </div>
                   <div className="lecture-footer">
                     <div className="bg-cc footer-image"></div>
                   </div>
                 </div>
                 )}
                 renderRow={row}
                 className="UserFinishActivity-list"
                 pageSize={5}
                 // useBodyScroll
                 style={{
                   height: 'calc(100vh - 90px) ',
                   overflow: 'auto',
                 }}
                 onScroll={() => { }}
                 scrollRenderAheadDistance={500}
                 onEndReached={this.onEndReached}
                 onEndReachedThreshold={10}

               />
        </div>
        <div className="lecture-footer">
          <div className="bg-cc footer-image"></div>
        </div>
      </div>


    );
  }
}

export default UserFinishActivity;



