import React, { Component } from 'react';
import './signUp.css';
import { ListView, Toast } from 'antd-mobile';
import { Link, Redirect } from 'react-router-dom';
import { Activity, User } from '../api'
const pathPrefix = window._config.pathPrefix;
const activityTypes = {
  adult:"成年人活动",
  minor:"未成年人活动",
  "parent-child":"亲子活动"
}
const grades = ['幼儿园小班','幼儿园中班','幼儿园大班','一年级','二年级','三年级','四年级','五年级','六年级','七年级','八年级','九年级','高一','高二','高三'];

class signUp extends Component {
    constructor(props){
        super(props);
        //console.log(this.props);
        const dataSource = new ListView.DataSource({
          rowHasChanged: (row1, row2) => row1 !== row2,
        });

        this.state = {
          activityContent: {},
          redirect: false,
          items:[],
          pitchState: [],
          dataSource,
          pitchCount: 0,
          editRedirect: false,
          editData: {},

          bookingDisabled: false,
        };
    }

    timestampToTime = (timestamp) => {
          let date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
          let Y = date.getFullYear() + '-';
          let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
          let D = (date.getDate() < 10 ? '0' +(date.getDate()) : date.getDate())   + ' ';
          let h = date.getHours() + ':';
          let m = date.getMinutes();
          // let s = date.getSeconds();
          let week = this.getweekDay(new Date(Y+M+D));
          let time = (h<=12) ? "上午" : "下午";
          return Y+M+D+"("+week+") "+time+h+m;
    }

    getweekDay = (date) => {
      let week;
      if(date.getDay()===0) week="星期天"
      if(date.getDay()===1) week="星期一"
      if(date.getDay()===2) week="星期二"
      if(date.getDay()===3) week="星期三"
      if(date.getDay()===4) week="星期四"
      if(date.getDay()===5) week="星期五"
      if(date.getDay()===6) week="星期六"
      return week;
    }

    getGrade = (value) => {
      return grades[value];
    }

    async requestActivityData () {
      try{
        let data = await Activity.activityContent({
          _id : this.props.location.state.id,
        });

        if(data && data.code===true){
          //console.log(data.data);
          this.setState({
            activityContent: data.data
          });
        }
      }catch(e){
        //
      }
    }

    async requestPeopleInfoList () {
      try{
        let data = await User.getUserList({
        });
        //console.log(data);

        if(data){
          let pitchStateArr=[];
          for (var k = 0, length = data.length; k < length; k++) {
            pitchStateArr.push(false);
          }
          this.setState({
            items: data,
            dataSource: this.state.dataSource.cloneWithRows(data),
            pitchState:pitchStateArr
          })
        }
      }catch(e){
        //
      }
    }

    //耗时操作放在这里面
    async componentDidMount(){
      try{
        this.requestActivityData();
        this.requestPeopleInfoList();

      }catch(e){
        //
      }
    }

    cell_click = (index) => {
      if (this.state.pitchState[index]) {
        let Count = this.state.pitchCount - 1;
        this.setState({
          pitchCount:Count
        });
      }else {
        let Count = this.state.pitchCount + 1;
        this.setState({
          pitchCount:Count
        });
      }
      let pitchStateArr = this.state.pitchState;
      pitchStateArr[index] = !pitchStateArr[index];
      this.setState({
        pitchState:pitchStateArr
      });
    }


    async delPeopleInfo (id) {
      try{
        let data = await User.delUser({
          _id: id
        });
        if(data){
          Toast.success(data.msg, 2, () => {
            this.requestPeopleInfoList();

          });
        }
      }catch(e){
        //
      }
    }

    deletePeopleInfo = (index) => {
        //console.log(index);
        this.delPeopleInfo(this.state.items[index]._id);
    }

    editPeopleInfo = (index) => {
        //console.log(index);
        this.setState({
          editRedirect: true,
          editData: this.state.items[index]
        })
    }

    BookingData = async (users) => {
      try{
        this.setState({
          bookingDisabled: false
        });
        let data = await Activity.booking({
          _id: this.props.location.state.id,
          users:users
        });
        //console.log(data);
        if (data) {
          Toast.success(data.msg, 2, () => {
            if (data.code === true) {
              this.setState({
                redirect: true,
              });
            }
          });
        }


      }catch(e){
        //

      }
    }

    Booking = () => {
      this.setState({
        bookingDisabled: true
      })

      let users = [];
      this.state.pitchState.map((item, index) => {
        if (item === true) {
          users.push(this.state.items[index]);
        }
        return false;
      })

      if (users.length > 0) {
        if (this.props.location.state.model === "lecture") {
          if (users.length > 2) {
              Toast.success("最多选择两名用户", 1, () => {
                this.setState({
                  bookingDisabled: false
                });
              });
          }else {
              //公益讲座 判断年龄限制
              let flag = false;
              for (var i = 0; i < users.length; i++) {
                if (users[i].age > this.state.activityContent.limit.age.max || users[i].age < this.state.activityContent.limit.age.min) {
                    flag = true;
                    break;
                }
              }

              if (flag) {
                Toast.success("请选择正确年龄段的用户报名", 1, () => {
                  this.setState({
                    bookingDisabled: false
                  });
                });
              }else {
                this.BookingData(users);
              }
          }
        }else {
          if (this.state.activityContent && this.state.activityContent.type === "parent-child") {
            if (users.length === 2 && ((users[0].type === 0 && users[1].type === 1) || (users[1].type === 0 && users[0].type === 1))) {
              this.BookingData(users);
            }else {
              Toast.success("亲子活动必选一个成年人和一个未成年人", 1, () => {
                this.setState({
                  bookingDisabled: false
                });
              });
            }

          }else if(this.state.activityContent && this.state.activityContent.type === "adult"){
            //成年人活动
            if (users.length > 1) {
              Toast.success("请选择一位用户报名参加", 1, () => {
                this.setState({
                  bookingDisabled: false
                });
              });
            }else {
              if (users[0].type === 1) {
                //成年人
                if (users[0].age > this.state.activityContent.limit.age.max || users[0].age < this.state.activityContent.limit.age.min) {
                  Toast.success("年龄不符合要求", 1, () => {
                    this.setState({
                      bookingDisabled: false
                    });
                  });
                }else {
                  this.BookingData(users);
                }
              }else {
                //未成年人
                Toast.success("请选择一位已成年用户报名参加", 1, () => {
                  this.setState({
                    bookingDisabled: false
                  });
                });
              }
            }
          }else {
            //未成年人活动
            if (users.length > 1) {
              Toast.success("请选择一位用户报名参加", 1, () => {
                this.setState({
                  bookingDisabled: false
                });
              });
            }else {
              if (users[0].type === 1) {
                //成年人
                Toast.success("请选择一位未成年用户报名参加", 1, () => {
                  this.setState({
                    bookingDisabled: false
                  });
                });
              }else {
                //未成年人
                if (this.state.activityContent.limit.type === 'age') {
                  if (users[0].age > this.state.activityContent.limit.age.max || users[0].age < this.state.activityContent.limit.age.min) {
                    Toast.success("年龄不符合要求", 1, () => {
                      this.setState({
                        bookingDisabled: false
                      });
                    });
                  }else {
                    this.BookingData(users);
                  }
                }else {
                  if (users[0].grade > this.state.activityContent.limit.grade.max || users[0].grade < this.state.activityContent.limit.grade.min) {
                    Toast.success("年级不符合要求", 1, () => {
                      this.setState({
                        bookingDisabled: false
                      });
                    });
                  }else {
                    this.BookingData(users);
                  }
                }
              }
            }
          }
        }
      }else {
        Toast.success("请选择报名参加的用户", 1, () => {
          this.setState({
            bookingDisabled: false
          });
        });
      }
    }


    render() {

      let index = 0;
      const row = (rowData, sectionID, rowID) => {
       if (index > this.state.items.length - 1) {
         index = 0;
       }
       const obj = this.state.items[index++];


       return (
           <div className="signUp-topContent-footer-cell" key={rowID} onClick={() => this.cell_click(rowID)}>
              <div className={this.state.pitchState[rowID] ? "cell-img-click" : "cell-img-normal"}></div>
              <div className={this.state.pitchState[rowID] ? "cell-name-click" : "cell-name-normal"}>{obj.name}（{obj.type === 0 ? "未成年人" : "成年人"}）</div>
              <div className="cell-delete" onClick={(e) => { e.stopPropagation(); e.preventDefault();  this.deletePeopleInfo(rowID);}}></div>
              <div className="cell-write"  onClick={(e) => { e.stopPropagation(); e.preventDefault();  this.editPeopleInfo(rowID);}}></div>
           </div>
       );
     };

     let pathname = `${pathPrefix}/PeopleInfo`;

      if (this.state.redirect) {
        return <Redirect push to="/" />; //or <Redirect push to="/sample?a=xxx&b=yyy" /> 传递更多参数
      }

      if (this.state.editRedirect) {
        let editLinkData = {
          id: this.props.location.state.id,
          Info: this.state.editData
        }
        let editPath = {
         pathname:pathname,
         state:editLinkData,
        }
        return <Redirect push to={editPath} />; //or <Redirect push to="/sample?a=xxx&b=yyy" /> 传递更多参数
      }

      let data = this.state.activityContent || {};

      return(
          <div className="signUp-home">
            <div className="signUp-topContent">
              <div>{data.title}</div>
              <div style={{display:`${this.props.location.state.model === "lecture" ? "block" : "none"}`}}>主讲嘉宾：<span className="signUp-topContent-span">{data.speaker}</span></div>
              <div>时间：<span className="signUp-topContent-span">{data.time ? this.timestampToTime(data.time.start): ''}</span></div>
              <div>地点：<span className="signUp-topContent-span">{data.address}</span></div>
              <div style={{display:`${this.props.location.state.model === "lecture" ? "none" : "block"}`}}>活动类型：<span className="signUp-topContent-span">{activityTypes[data.type]}</span></div>
              <div>{this.props.location.state.model === "lecture" ? "适合年龄段：" : "活动对象："}<span className="signUp-topContent-span">{data.limit? ((data.limit.type === 'age') ? (data.limit.age.min + "岁~" + data.limit.age.max + "岁") : (this.getGrade(data.limit.grade.min) + "~" + this.getGrade(data.limit.grade.max))) : ""}</span></div>
            </div>
            <div className="signUp-topContent-line"></div>
            <div className="signUp-topContent-remark">{this.props.location.state.model === "lecture" ? "备注：参与者最多只能选择两个人" : "备注：亲子活动必选一个成年人和一个未成年人"}</div>
            <div className="signUp-topContent-line"></div>
            <div className="signUp-topContent-footer">
              <div className="signUp-topContent-footer-top">
                <div className="signUp-topContent-footer-numPeopleTitle">参与者<span className="signUp-topContent-footer-numPeople">（已选{this.state.pitchCount}人）</span></div>
                <Link to={pathname}>
                  <div className="signUp-topContent-footer-addPeople">新增人员<div className="signUp-topContent-footer-addPeople-img"></div></div>
                </Link>
              </div>
              <div className="signUp-topContent-footer-prompt">不符合活动要求以及黑名单用户不可选择</div>
              <ListView
                     ref={el => this.lv = el}
                     dataSource={this.state.dataSource}
                     renderRow={row}
                     className="signUp-list"
                     pageSize={5}
                     useBodyScroll
                     onScroll={() => {  }}
                     scrollRenderAheadDistance={500}
                     onEndReached={this.onEndReached}
                     onEndReachedThreshold={10}
                   />
            </div>

            <button className="signUp-baoming" onClick={this.Booking} disabled={this.state.bookingDisabled}>确认报名</button>
          </div>
      );
    }
}

export default signUp;



