import React, { Component } from 'react';
import './Home.css';
import { Tabs } from 'antd-mobile';
import LectureModel from './LectureModel.js'
import ActivityModel from './ActivityModel.js'
import { Link } from 'react-router-dom';
import { User } from '../api';
const pathPrefix = window._config.pathPrefix;
const apiPrefix = window._config.apiPrefix;

const tabs = [
  {title: "公益讲座"},
  {title: "教育活动"},
];

class Home extends Component {
  constructor(props) {
    super(props);
      this.state = {
        index: 0,
        lectureURl: apiPrefix + '/activity/select?category=lecture&time=future',
        activityURl: apiPrefix + '/activity/select?category=education&time=future',
        userInfo: {}
      };
    }

    //耗时操作放在这里面
    async componentDidMount(){
      try{
        let data = await User.getMyInfo({

        });

        if(data && data.code === true){
            this.setState({
              userInfo: data.data
            })
        }
      }catch(e){
        //
      }
    }


    getInitialState = function () {
        return {
          checked: true
        };
      }
  render() {
    return (
      <div className="Home">
        <div className="NavHead">
          <div className="NavHead-tab">
            <Tabs
              tabs={tabs}
              initialPage={0}
              onChange={(tab, index) => {

              }}
              onTabClick={(tab, index) => {
                this.setState({
                  index : index
                })
              }}
              swipeable={false}
            >
            </Tabs>
          </div>
          <Link to={`${pathPrefix}/userCenter`}>
            <div className="NavHead-user">
              <div className="NavHead-user-headimgurl" style={{background: `url(${this.state.userInfo.headimgurl}) center no-repeat / cover`}}></div>
            </div>
          </Link>
        </div>
        {(this.state.index ===  0) ? (<LectureModel stateUrl={this.state.lectureURl}/>) : null}
        {(this.state.index ===  1) ? (<ActivityModel stateUrl={this.state.activityURl}/>) : null}

      </div>
    );
  }
}

export default Home;



