import Home         from '../Home/Home.js'
import LectureContent    from '../LectureContent/LectureContent.js'
import ActivityContent    from '../ActivityContent/ActivityContent.js'
import signUp    from '../signUp/signUp.js'
import PeopleInfo    from '../PeopleInfo/PeopleInfo.js'
import userCenter from '../userCenter/userCenter.js'
import CancelActivity from '../userCenter/CancelActivity.js'

const pathPrefix = window._config.pathPrefix;

const routes = [
    {
        path: pathPrefix + '/activityHome',
        component: Home,
    },
    {
        path: pathPrefix + '/LectureContent/:id',
        component: LectureContent,
    },
    {
        path: pathPrefix + '/ActivityContent/:id',
        component: ActivityContent,
    },
    {
        path: pathPrefix + '/signUp/:id',
        component: signUp,
    },
    {
        path: pathPrefix + '/PeopleInfo',
        component: PeopleInfo,
    },
    {
        path: pathPrefix + '/userCenter',
        component: userCenter,
    },
    {
        path: pathPrefix + '/CancelActivity/:id',
        component: CancelActivity,
    }

]
export default routes;



