export const Activity = {
    list: ApiCreator(activity.list),
    activityContent: ApiCreator(activity.activityContent),
    booking: ApiCreator(activity.booking),
    cancelBooked: ApiCreator(activity.cancelBooked),

}

export const User = {
    getMyInfo: ApiCreator(user.getMyInfo),
    addUser: ApiCreator(user.addUser),   //添加参与活动人员信息
    getUserList:  ApiCreator(user.getUserList),   //获取已添加的人员列表
    delUser:  ApiCreator(user.delUser),   //删除参与活动人员信息
    editUser:  ApiCreator(user.editUser),  //修改参与活动人员信息
    getMyBooked: ApiCreator(user.getMyBooked),
    getMyBookeds: ApiCreator(user.getMyBookeds)

}



