import React, { Component } from 'react';
import './userCenter.css';
import { Tabs } from 'antd-mobile';
import UserCurrentActivity from './UserCurrentActivity.js'
import UserFinishActivity from './UserFinishActivity.js'
import { Link } from 'react-router-dom';
const pathPrefix = window._config.pathPrefix;


const tabs = [
  {title: "当期预约"},
  {title: "历史预约"},
];
class userCenter extends Component {

  constructor(props){
      super(props);

      this.state = {
        index: 0,
        currentActivityURl: 'http://api.kzl.la/v2/events',
      };
  }
  render() {
    return(
      <div className="userCenter-home">
        <div className="Nav">
          <div className="Nav-top">我的活动预约
            <Link to={`${pathPrefix}/`}>
              <div className="Nav-top-back"></div>
            </Link>
          </div>
          <div className="Nav-bottom">
            <Tabs
              tabs={tabs}
              initialPage={0}
              onChange={(tab, index) => {
              }}
              onTabClick={(tab, index) => {
                this.setState({
                  index : index
                })
              }}
              swipeable={false}
            >
            </Tabs>
          </div>
        </div>
        {(this.state.index ===  0) ? (<UserCurrentActivity/>) : null}
        {(this.state.index ===  1) ? (<UserFinishActivity/>) : null}

      </div>
    );
  }
}

export default userCenter;



