import 'whatwg-fetch';
// import { message } from 'antd';
// import history from '../router/history';
// const loginPath = `${window._config._prefix.link}/login`;


const donate = window._config.donate;
const Catch = async (error) => {
    if(error.response.status===401){
      let myUri = window.location.href;

      let res = await fetchJson(donate + "/wechat/getOauthRedirect",{
        redirect: donate + "/wechat/login?redirect=" + encodeURIComponent(myUri),
        state:"state"
      });
      if(res && res.code){
        window.location.href = res.uri;
      }else{
        alert('网络错误！');
      }
    }
}

const getData = (args)=>{
    let data = new FormData();
    for(let i in args){
        data.append(i, args[i]);
    }
    return data;
}

export const fetchFile = (url, args) => {
    let data = getData(args);
    return fetch(url, {
        method: 'post',
        mode: 'cors',
        credentials: 'include',
        body: data
    })
    .then((response) => {
        if(response.status >= 200 && response.status < 300){
            return response.json();
        }else{
            let error = new Error(response.statusText);
            error.response = response;
            throw error;
        }
    })
    .then((data) => {
        return data;
    })
    .catch((error) => {
        Catch(error);
    });
}
export const fetchJson = (url, args)=>{
    return fetch(url, {
        method: 'post',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
        mode: 'cors',
        credentials: 'include',
        body: JSON.stringify(args)
    })
    .then((response) => {
        if(response.status >= 200 && response.status < 300){
            return response.json();
        }else{
            let error = new Error(response.statusText);
            error.response = response;
            throw error;
        }
    })
    .then((data) => {
        return data;
    })
    .catch((error) => {
        Catch(error);
    });
}



